import { request } from 'utils';

const prefix = '/api/students';

export const getPaperStudent = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Paper_Student/SearchPapersStudent',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getPaperStudentById = ({
  studentId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Paper_Student/BrowsePapersByStudent',
    method: 'GET',
    params: {
      studentId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const updateStudentPaper = async ({
  id,
  paperTypeId,
  studentId,
  recipientId,
  receivedDate,
  returnById,
  returnedDate,
}) => {
  const res = await request({
    prefix,
    url: '/Student/'.concat(id).concat('/ReturnPaper'),
    method: 'POST',
    params: {},
    data: { id, paperTypeId, studentId, recipientId, receivedDate, returnById, returnedDate },
  });
  return res;
};



